  text-decoration: none;
  transition: all .3s ease;
}

a.link {
  color: #25c2ef;
}

.button {
  width: 12.4em;
  background: #25c2ef;
  color: white;
  cursor: pointer;
  display: block;
  font-size: 1em;
  font-weight: 400;
  line-height: 3.2;
  margin: 0 auto;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  vertical-align: middle;
  transition: background .3s ease;
  border: none;
  border-bottom: 2px solid #00abdc;
}

.button span {
  z-index: 20;
}

@media (max-width: 40em) {
  .button:hover,
  .button:focus {
    background: #00abdc;
  }
}

.button:active {
  top: 2px;
  border-bottom: 2px solid transparent;
  transition: top .3s linear;
}

.button--primary {
  background: #ef6000;
  border-bottom: 2px solid #e34f00;
}

@media (max-width: 40em) {
  .button--primary:hover,
  .button--primary:focus {
    background: #e34f00;
  }
}

.button--secondary {
  background: #f9ae3b;
  border-bottom: 2px solid #f79908;
}

@media (max-width: 40em) {
  .button--secondary:hover,
  .button--secondary:focus {
    background: #f79908;
  }
}

.button--tertiary {
  background: #2d2d2d;
  border-bottom: 2px solid black;
}

@media (max-width: 40em) {
  .button--tertiary:hover,
  .button--tertiary:focus {
    background: black;
  }
}

.button--quarternary {
  background: transparent;
  color: #2d2d2d;
  border: 2px solid #2d2d2d;
}

.button--quarternary:hover,
.button--quarternary:focus,
.button--quarternary:active {
  background: #2d2d2d;
  color: white;
}

.button:disabled,
.button.disabled {
  background: rgba(45, 45, 45, 0.66);
  color: #f2f2f2;
}

@media (min-width: 40em) {
  .button {
    overflow: hidden;
    position: relative;
  }
  .button:after {
    background: rgba(240, 240, 240, 0.25);
    content: "";
    height: 155px;
    left: -75px;
    position: absolute;
    top: -50px;
    transform: rotate(35deg);
    transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
    width: 50px;
    z-index: 1;
  }
  .button:hover:after,
  .button:focus:after {
    left: 120%;
    transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
  }
}

